html, body, #root, .App {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  background-color: #f4f4f4;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  user-select: none;
  touch-action: none;
}

.flipbook {
  background-color: white;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

/* Flipbook page */
.page {
  width: 100%;
  height: 100%;
  background-color: white;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Image fits inside the page container with NO crop */
.flip-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  pointer-events: none;
  user-select: none;
  border-radius: 4px;
  background: white;
}
  
/* Navigation arrows */
.nav-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1000;
  background: rgba(255, 255, 255, 0.8);
  border: none;
  font-size: 2rem;
  padding: 10px 15px;
  color: black;
  cursor: pointer;
  border-radius: 50%;
}

.prev {
  left: 20px;
}

.next {
  right: 20px;
}
